import { ImageCropComponent } from '../../common/components/image-crop/image-crop.component';
import { ImageFileReader } from '../../common/utils/ImageFileReader';
import { switchMap, tap } from 'rxjs/operators';
import { Component, OnInit, ChangeDetectionStrategy, ChangeDetectorRef, ViewChild } from '@angular/core';
import { of } from 'rxjs';

@Component({
  standalone: false,
  selector: 'app-crop',
  templateUrl: './crop.component.html',
  styleUrls: ['./crop.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CropComponent implements OnInit {
  isScanning: boolean;
  isBeingCropped = false;
  imageData: string;

  private cropComponent: ImageCropComponent;
  @ViewChild('cropComponent') set content(cropComponent: ImageCropComponent) {
    this.cropComponent = cropComponent;
  }

  constructor(
    private readonly cdr: ChangeDetectorRef) { }

  ngOnInit(): void {
  }

  private scanFile(image: File) {
    of(1)
    .pipe(
      tap(() => {
        this.isScanning = true;
        this.cdr.markForCheck();
      }),
      switchMap(() => ImageFileReader.convertFileToImageBase64(image)),
      tap((result) => {
        this.imageData = result.imageData;
        this.isScanning = false;
        this.cdr.markForCheck();
      }),
    )
    .subscribe();
  }

  fileChanged(event: Event) {
    const loadedFileElement = event.target as HTMLInputElement;
    const file = loadedFileElement.files[0];
    this.scanFile(file);
  }

  onImageCrop(imageB64: string) {
    this.imageData = imageB64;
    this.cdr.detectChanges();
  }

  onDownloadClick() {
    const imageType = this.imageData.match(/^data:image\/([^;]+)/)[1];
    this.downloadBase64File(this.imageData, `cropped-image.${imageType}`);
  }

  downloadBase64File(base64Url: string, fileName: string) {
    const downloadLink = document.createElement('a');
    downloadLink.href = base64Url;
    downloadLink.download = fileName;
    downloadLink.click();
    this.isScanning = true;
  }

  onIsCroppingChanged(isBeingCropped: boolean) {
    this.isBeingCropped = isBeingCropped;
    this.cdr.detectChanges();
  }

  onCropClicked() {
    if(!this.cropComponent) { return; }
    this.cropComponent.cropImage();
  }

  get isLoading() {
    return this.isScanning || this.isBeingCropped;
  }
}
